@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');
*,
*:before,
*:after {
    box-sizing: border-box;
}
* {
    margin: 0;
    padding: 0;
    font: inherit;
}
img,
picture,
svg,
video {
    display: block;
    max-width: 100%;
}
input,
select,
textarea {
    background-color: transparent;
    outline: none;
}
button {
    cursor: pointer;
    background-color: transparent;
    outline: none;
    border: 0;
}
body {
    font-family: 'Roboto', sans-serif;
    min-height: 100vh;
    font-weight: 400;
    font-size: 16px;
    line-height: 1;
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
